import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators, FormArray } from '@angular/forms';
import { StudentDocuments } from 'src/app/models/student-docs/student-docs';
import { AlertService } from 'src/app/services/alert/alert.service';
import { GeneralizedClientService } from 'src/app/services/generalized-client/generalized-client.service';
import { ActivatedRoute, Router } from '@angular/router';
import { OnBoardingModel } from 'src/app/models/on-boarding/on-boarding';


@Component({
  selector: 'app-on-boarding-view',
  templateUrl: './on-boarding-view.component.html',
  styleUrls: ['./on-boarding-view.component.less']
})
export class OnBoardingViewComponent implements OnInit {

  public onBoardingViewForm: FormGroup;
  public internationalCheckboxesShown = false;
  public domesticCheckboxesShown = false;
  public dob: Date;
  internationalDocs: StudentDocuments[] = [];
  domesticDocs: StudentDocuments[] = [];
  categories: string[] = ['Domestic', 'International'];
  updateStudent: OnBoardingModel[] = [];
/*
  Constructor for On-Boarding view page it uses alertService, activatedRoute and router service
*/
  constructor(private fb: FormBuilder, private alertService: AlertService, private activatedRoute: ActivatedRoute,
              public documentService: GeneralizedClientService, public router: Router
  ) {
    this.onBoardingViewForm = fb.group({
      sname: new FormControl({ value: '', disabled: true }, Validators.compose([Validators.required, Validators.minLength(3)])),
      category: new FormControl({ value: '0', disabled: true }),
      internationalDocs: new FormArray([]),
      domesticDocs: new FormArray([]),
      dob: new FormControl({ value: null, disabled: true }),
      fathername: new FormControl({ value: '', disabled: true }, Validators.compose([Validators.required, Validators.minLength(3)])),
      mothername: new FormControl({ value: '', disabled: true }, Validators.compose([Validators.required, Validators.minLength(3)])),
      lastclassscore: new FormControl({ value: '', disabled: true }, Validators.compose(
        [Validators.required, Validators.maxLength(3), Validators.max(100), Validators.min(1)])),

    });
    this.documentService.getInternationalStudentDocuments().subscribe((res: any) => {
      this.internationalDocs = res.internationalStudentDocuments;
      localStorage.internationalDocs = JSON.stringify(this.internationalDocs);
    });
    this.documentService.getDomesticStudentDocuments().subscribe((res: any) => {
      this.domesticDocs = res.domesticStudentDocuments;
      localStorage.domesticDocs = JSON.stringify(this.domesticDocs);
    });
  }

  /*
    ngOnInit component
  */
  ngOnInit() {
    this.navigatedFromListingScreen();
  }

  /*
    It resets the documents array
  */
  resetDocs() {
    const domesticDocsArray = (this.onBoardingViewForm.controls.domesticDocs as FormArray);
    while (domesticDocsArray.length !== 0) {
      domesticDocsArray.removeAt(0);
    }
    const internationalDocsArray = (this.onBoardingViewForm.controls.internationalDocs as FormArray);
    while (internationalDocsArray.length !== 0) {
      internationalDocsArray.removeAt(0);
    }
  }

  /*
    It sets checkboxes control when the category is international
  */
  addInternationalCheckboxes() {
    this.internationalDocs.map(() => {
      const control = new FormControl({ value: '', disabled: true });
      (this.onBoardingViewForm.controls.internationalDocs as FormArray).push(control);
    });
  }

  /*
    It sets checkboxes control when the category is domestic
  */
  addDomesticCheckboxes() {
    this.domesticDocs.map(() => {
      const control = new FormControl({ value: '', disabled: true });
      (this.onBoardingViewForm.controls.domesticDocs as FormArray).push(control);
    });
  }

  /*
    It is used when cancel button is clicked
  */
  callingCancel() {
    this.onClose();
  }

  /*
    Generalized method for navigating to the students list
  */
  onClose() {
    localStorage.setItem('checkDomesticDocView', JSON.stringify(false));
    localStorage.setItem('checkInternationalDocView', JSON.stringify(false));
    this.router.navigate(['/mainPage/studentsList']);
  }

  /*
    Used to set values when navigated from students listing screen
  */
  navigatedFromListingScreen() {
    this.activatedRoute.params.subscribe(params => {
      if (params.id != null && params.id !== '' && params.id !== undefined) {
        const sList = JSON.parse(localStorage.getItem('studentsList'));
        this.updateStudent = sList.filter(value => value.id === params.id);
        this.onBoardingViewForm.controls.sname.setValue(this.updateStudent[0].sname);
        this.onBoardingViewForm.controls.category.setValue(this.updateStudent[0].category);
        if (JSON.parse(localStorage.getItem('checkInternationalDocView'))) {
          this.internationalDocs = JSON.parse(localStorage.getItem('internationalDocs'));
          this.internationalCheckboxesShown = true;
          this.domesticCheckboxesShown = false;
          this.resetDocs();
          this.addInternationalCheckboxes();
          this.onBoardingViewForm.controls.internationalDocs.setValue(this.updateStudent[0].internationalDocs);
        } else if (JSON.parse(localStorage.getItem('checkDomesticDocView'))) {
          this.domesticDocs = JSON.parse(localStorage.getItem('domesticDocs'));
          this.domesticCheckboxesShown = true;
          this.internationalCheckboxesShown = false;
          this.resetDocs();
          this.addDomesticCheckboxes();
          this.onBoardingViewForm.controls.domesticDocs.setValue(this.updateStudent[0].domesticDocs);
        }
        this.onBoardingViewForm.controls.dob.setValue(new Date(this.updateStudent[0].dob));
        this.onBoardingViewForm.controls.fathername.setValue(this.updateStudent[0].fathername);
        this.onBoardingViewForm.controls.mothername.setValue(this.updateStudent[0].mothername);
        this.onBoardingViewForm.controls.lastclassscore.setValue(this.updateStudent[0].lastclassscore);

      }
    });
  }
}
