import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { AlertService } from 'src/app/services/alert/alert.service';
import { Alert, AlertType } from 'src/app/models/alert/alert.model';
import { MessageConstants } from 'src/app/app.component.data';
import { AdminLogin } from 'src/app/models/admin-login/admin.login';
import { GeneralizedClientService } from 'src/app/services/generalized-client/generalized-client.service';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.less']
})
export class AdminLoginComponent implements OnInit {

  public loginForm: FormGroup;
  public errorCount: number;
  public saveData = false;
  adminUsersList: AdminLogin[] = [];
  resultAdminCheck: AdminLogin[];
  /*
  Admin Login Validations
  */
  constructor(private fb: FormBuilder, private alertService: AlertService, public adminLoginService: GeneralizedClientService,
              private router: Router) {
    this.loginForm = fb.group({
      uname: ['', Validators.required],
      psw: ['', Validators.compose([Validators.required])],
    });

    this.adminLoginService.getAdminUsers().subscribe((res: any) => {
      this.adminUsersList = res.admins;
    });
  }
  ngOnInit() { }

  /*
    This is used for Admin Login- It will check validations and will redirect to the mainpage
   */
  callingLogin() {
    this.errorCount = 0;
    this.saveData = true;
    this.validateAllFormFields(this.loginForm);

    if (this.errorCount !== 0) {
      this.alertService.sendAlert(new Alert(MessageConstants.formErrorMsg, AlertType.Error));
      return;
    } else {
      const adminUserModel: AdminLogin = new AdminLogin();
      adminUserModel.username = this.loginForm.controls.uname.value;
      adminUserModel.password = this.loginForm.controls.psw.value;

      if (this.adminUsersList.length > 0) {
        this.resultAdminCheck = this.adminUsersList.filter
          (x => x.username === adminUserModel.username && x.password === adminUserModel.password);
      }
      if (this.resultAdminCheck.length === 1) {
        this.router.navigate(['mainPage']);
        localStorage.setItem('currentUser', adminUserModel.username);

      } else {
        this.alertService.sendAlert(new Alert(MessageConstants.invalidUserErrorMsg, AlertType.Error));
      }
    }
  }

  /*
  This is used for reseting the form
 */
  callingReset() {
    this.loginForm.controls.uname.setValue('');
    this.loginForm.controls.psw.setValue('');
  }

  /*
  Used in validateAllFormFields
  */
  isFieldNotValid(control: FormControl, field: string) {
    return (!(control.valid || control.disabled) && (control.touched || this.saveData));
  }

  /*
  This function is created to count the validation error count
  */
  validateAllFormFields(formGroup: FormGroup): number {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        if (this.isFieldNotValid(control, field)) {
          this.errorCount++;
        }
      } else if (control instanceof FormGroup) {
        return this.validateAllFormFields(control);
      }
    });
    return this.errorCount;
  }
}
