import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators, FormBuilder, FormArray } from '@angular/forms';
import { AlertService } from 'src/app/services/alert/alert.service';
import { GeneralizedClientService } from 'src/app/services/generalized-client/generalized-client.service';
import { Alert, AlertType } from 'src/app/models/alert/alert.model';
import { MessageConstants } from 'src/app/app.component.data';
import { OnBoardingModel } from 'src/app/models/on-boarding/on-boarding';
import { Guid } from 'src/app/app.guid';
import { StudentDocuments } from 'src/app/models/student-docs/student-docs';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-on-boarding-form',
  templateUrl: './on-boarding-form.component.html',
  styleUrls: ['./on-boarding-form.component.less']
})
export class OnBoardingFormComponent implements OnInit {
  public onBoardingForm: FormGroup;
  public errorCount: number;
  public saveData = false;
  public internationalCheckboxesShown = false;
  public domesticCheckboxesShown = false;
  public dob: Date;
  categories: string[] = ['Domestic', 'International'];
  internationalDocs: StudentDocuments[] = [];
  domesticDocs: StudentDocuments[] = [];
  public listOfOnBoardingStudents: OnBoardingModel[] = [];
  public id: Guid;
  public testarr: boolean[] = [];
  editCalled = false;
  checked = false;
  updateStudent: OnBoardingModel[] = [];

  /*
  Constructor for On-Boarding form it uses alertservice, activatedRoute and router services
  */
  constructor(private fb: FormBuilder, private alertService: AlertService, public activatedRoute: ActivatedRoute,
              public documentService: GeneralizedClientService, private router: Router
  ) {
    this.onBoardingForm = fb.group({
      sname: new FormControl('', Validators.compose([Validators.required, Validators.minLength(3)])),
      category: new FormControl({ value: '0', disabled: false }),
      internationalDocs: new FormArray([]),
      domesticDocs: new FormArray([]),
      dob: null,
      fathername: new FormControl('', Validators.compose([Validators.required, Validators.minLength(3)])),
      mothername: new FormControl('', Validators.compose([Validators.required, Validators.minLength(3)])),
      lastclassscore: new FormControl('', Validators.compose(
        [Validators.required, Validators.maxLength(3), Validators.max(100), Validators.min(1)])),

    });
    this.documentService.getInternationalStudentDocuments().subscribe((res: any) => {
      this.internationalDocs = res.internationalStudentDocuments;
      localStorage.internationalDocs = JSON.stringify(this.internationalDocs);
    });
    this.documentService.getDomesticStudentDocuments().subscribe((res: any) => {
      this.domesticDocs = res.domesticStudentDocuments;
      localStorage.domesticDocs = JSON.stringify(this.domesticDocs);
    });
  }

  /*
    ngOnInit component
  */
  ngOnInit() {
    this.subcribeToFormChanges();
    this.editCalled = JSON.parse(localStorage.getItem('editCalled'));
    this.navigatedFromListingScreen();
  }
  /*
    It subscribe to any form change mainly used to subscribe changes of category component
  */
  subcribeToFormChanges(): any {
    const categoryValueChanges$ = this.onBoardingForm.controls.category.valueChanges;
    categoryValueChanges$.subscribe(element => {
      if (element) {
        if (this.onBoardingForm.controls.category.value === 'International') {
          this.internationalCheckboxesShown = true;
          this.domesticCheckboxesShown = false;
          this.resetDocs();
          this.addInternationalCheckboxes();
        } else if (this.onBoardingForm.controls.category.value === 'Domestic') {
          this.domesticCheckboxesShown = true;
          this.internationalCheckboxesShown = false;
          this.resetDocs();
          this.addDomesticCheckboxes();

        } else {
          this.domesticCheckboxesShown = false;
          this.internationalCheckboxesShown = false;
        }

      }
    });
  }

  /*
    It resets the documents array
  */
  resetDocs() {
    const domesticDocsArray = (this.onBoardingForm.controls.domesticDocs as FormArray);
    while (domesticDocsArray.length !== 0) {
      domesticDocsArray.removeAt(0);
    }
    const internationalDocsArray = (this.onBoardingForm.controls.internationalDocs as FormArray);
    while (internationalDocsArray.length !== 0) {
      internationalDocsArray.removeAt(0);
    }
  }

  /*
    It will add the internationaly checkboxes array
  */
  addInternationalCheckboxes() {
    this.internationalDocs.map((doc) => {
      let control;
      if (doc.isMandatory) {
        control = new FormControl(false, Validators.compose([Validators.requiredTrue]));
      } else {
        control = new FormControl(false);
      }
      (this.onBoardingForm.controls.internationalDocs as FormArray).push(control);
    });
  }


  /*
    It will add the domestic checkboxes array
  */
  addDomesticCheckboxes() {
    this.domesticDocs.map((doc) => {
      let control;
      if (doc.isMandatory) {
        control = new FormControl(false, Validators.compose([Validators.requiredTrue]));
      } else {
        control = new FormControl(false);
      }
      (this.onBoardingForm.controls.domesticDocs as FormArray).push(control);
    });
  }

  /*
    Used within validateAllFormFields
  */

  isFieldNotValid(control: FormControl, field: string) {
    return (!(control.valid || control.disabled) && (control.touched || this.saveData));
  }

  /*
  This function is created to count the validation error count
  */
  validateAllFormFields(formGroup: any): number {
    Object.keys(formGroup.controls).forEach(field => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        if (this.isFieldNotValid(control, field)) {
          this.errorCount++;
        }
      } else if (control instanceof FormGroup) {
        return this.validateAllFormFields(control);
      } else if (control instanceof FormArray) {
        return this.validateAllFormFields(control);
      }
    });
    return this.errorCount;
  }

/*
  When On Boarding button is clicked
*/
  callingOnBoarding() {
    this.errorCount = 0;
    this.saveData = true;
    this.validateAllFormFields(this.onBoardingForm);

    if (this.errorCount !== 0) {
      this.alertService.sendAlert(new Alert(MessageConstants.formErrorMsg, AlertType.Error));
      return;
    } else {
      const result: OnBoardingModel = new OnBoardingModel();
      result.sname = this.onBoardingForm.controls.sname.value;
      result.category = this.onBoardingForm.controls.category.value;
      result.docs = [];
      if (this.onBoardingForm.controls.category.value === 'International') {
        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < this.internationalDocs.length; i++) {
          this.testarr = this.onBoardingForm.controls.internationalDocs.value;
          if (this.testarr[i] != null && this.testarr[i] === true) {
            result.docs.push(this.internationalDocs[i].documentName);
          }
        }
      } else {
        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < this.domesticDocs.length; i++) {
          this.testarr = this.onBoardingForm.controls.domesticDocs.value;
          if (this.testarr[i] !== null && this.testarr[i] === true) {
            result.docs.push(this.domesticDocs[i].documentName);
          }
        }
      }
      result.dob = this.onBoardingForm.controls.dob.value;
      result.fathername = this.onBoardingForm.controls.fathername.value;
      result.mothername = this.onBoardingForm.controls.mothername.value;
      result.lastclassscore = this.onBoardingForm.controls.lastclassscore.value;
      this.id = Guid.newGuid();
      result.id = this.id.toString();
      result.internationalDocs = this.onBoardingForm.controls.internationalDocs.value;
      result.domesticDocs = this.onBoardingForm.controls.domesticDocs.value;
      this.listOfOnBoardingStudents.push(result);
      if (JSON.parse(localStorage.getItem('studentsList')) === null) {
        localStorage.studentsList = JSON.stringify(JSON.parse(JSON.stringify(this.listOfOnBoardingStudents)));
      } else {
        // tslint:disable-next-line: no-shadowed-variable
        const result = JSON.parse(localStorage.getItem('studentsList'));
        result.push(JSON.parse(JSON.stringify(this.listOfOnBoardingStudents[this.listOfOnBoardingStudents.length - 1])));
        localStorage.studentsList = JSON.stringify(result);
      }
      this.onBoardingForm.reset();
      this.onBoardingForm.controls.category.setValue(0);
      this.domesticCheckboxesShown = false;
      this.internationalCheckboxesShown = false;

    }
  }


/*
  When On Boarding edit button is clicked
*/
  callingOnBoardingEdit() {
    this.errorCount = 0;
    this.saveData = true;
    this.validateAllFormFields(this.onBoardingForm);

    if (this.errorCount !== 0) {
      this.alertService.sendAlert(new Alert(MessageConstants.formErrorMsg, AlertType.Error));
      return;
    } else {
      const result: OnBoardingModel = new OnBoardingModel();
      result.sname = this.onBoardingForm.controls.sname.value;
      result.category = this.onBoardingForm.controls.category.value;
      result.docs = [];
      if (this.onBoardingForm.controls.category.value === 'International') {
        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < this.internationalDocs.length; i++) {
          this.testarr = this.onBoardingForm.controls.internationalDocs.value;
          if (this.testarr[i] != null && this.testarr[i] === true) {
            result.docs.push(this.internationalDocs[i].documentName);
          }
        }
      } else {
        // tslint:disable-next-line: prefer-for-of
        for (let i = 0; i < this.domesticDocs.length; i++) {
          this.testarr = this.onBoardingForm.controls.domesticDocs.value;
          if (this.testarr[i] != null && this.testarr[i] === true) {
            result.docs.push(this.domesticDocs[i].documentName);
          }
        }
      }
      result.dob = this.onBoardingForm.controls.dob.value;
      result.fathername = this.onBoardingForm.controls.fathername.value;
      result.mothername = this.onBoardingForm.controls.mothername.value;
      result.lastclassscore = this.onBoardingForm.controls.lastclassscore.value;
      result.id = JSON.parse(localStorage.getItem('editId'));
      result.internationalDocs = this.onBoardingForm.controls.internationalDocs.value;
      result.domesticDocs = this.onBoardingForm.controls.domesticDocs.value;
      this.listOfOnBoardingStudents = JSON.parse(localStorage.getItem('studentsList'));
      const index = this.listOfOnBoardingStudents.findIndex(res => res.id === result.id);
      this.listOfOnBoardingStudents[index] = result;
      localStorage.studentsList = JSON.stringify(JSON.parse(JSON.stringify(this.listOfOnBoardingStudents)));
      this.onBoardingForm.reset();
      this.onBoardingForm.controls.category.setValue(0);
      this.domesticCheckboxesShown = false;
      this.internationalCheckboxesShown = false;
      this.onClose();
    }
  }


/*
  Generalized method for reseting and navigating
*/
  onClose() {
    localStorage.setItem('editCalled', JSON.stringify(false));
    localStorage.setItem('checkDomesticDoc', JSON.stringify(false));
    localStorage.setItem('checkInternationalDoc', JSON.stringify(false));
    this.router.navigate(['/mainPage/studentsList']);
  }

  /*
    When Cancel button is clicked
  */
  callingCancel() {
    this.onClose();
  }

  /*
    Used to set fields value when navigated from Students Listing screen
  */
  navigatedFromListingScreen() {
    this.activatedRoute.params.subscribe(params => {
      if (params.id != null && params.id !== '' && params.id !== undefined) {
        const sList = JSON.parse(localStorage.getItem('studentsList'));
        this.updateStudent = sList.filter(value => value.id === params.id);
        this.onBoardingForm.controls.sname.setValue(this.updateStudent[0].sname);
        this.onBoardingForm.controls.category.setValue(this.updateStudent[0].category);
        if (JSON.parse(localStorage.getItem('checkInternationalDoc'))) {
          this.internationalDocs = JSON.parse(localStorage.getItem('internationalDocs'));
          this.internationalCheckboxesShown = true;
          this.domesticCheckboxesShown = false;
          this.resetDocs();
          this.addInternationalCheckboxes();
          this.onBoardingForm.controls.internationalDocs.setValue(this.updateStudent[0].internationalDocs);
        } else if (JSON.parse(localStorage.getItem('checkDomesticDoc'))) {
          this.domesticDocs = JSON.parse(localStorage.getItem('domesticDocs'));
          this.domesticCheckboxesShown = true;
          this.internationalCheckboxesShown = false;
          this.resetDocs();
          this.addDomesticCheckboxes();
          this.onBoardingForm.controls.domesticDocs.setValue(this.updateStudent[0].domesticDocs);
        }
        this.onBoardingForm.controls.dob.setValue(new Date(this.updateStudent[0].dob));
        this.onBoardingForm.controls.fathername.setValue(this.updateStudent[0].fathername);
        this.onBoardingForm.controls.mothername.setValue(this.updateStudent[0].mothername);
        this.onBoardingForm.controls.lastclassscore.setValue(this.updateStudent[0].lastclassscore);

      }
    });
  }
}
