import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { StudentDocuments } from 'src/app/models/student-docs/student-docs';
import { AdminLogin } from 'src/app/models/admin-login/admin.login';

@Injectable({
  providedIn: 'root'
})
export class GeneralizedClientService {

  constructor(private http: HttpClient) { }

  getAdminUsers() {
    const url = '/OnBoardingAppByChirag/assets/admin.json';
    // const url = '/assets/admin.json';
    const result = this.http.get<AdminLogin[]>(url);
    return result;
  }

  getInternationalStudentDocuments() {
    const url = '/OnBoardingAppByChirag/assets/internationalStudentDocs.json';
    // const url = '/assets/internationalStudentDocs.json';
    const result = this.http.get<StudentDocuments[]>(url);
    return result;
  }
  getDomesticStudentDocuments() {
    const url = '/OnBoardingAppByChirag/assets/domesticStudentDocs.json';
    // const url = '/assets/domesticStudentDocs.json';
    const result = this.http.get<StudentDocuments[]>(url);
    return result;
  }
}
