import { Component, OnInit, OnDestroy, ViewContainerRef } from '@angular/core';
import { Alert, AlertType } from 'src/app/models/alert/alert.model';
import { Subscription } from 'rxjs';
import { AlertService } from 'src/app/services/alert/alert.service';
import { ToastrManager } from 'ng6-toastr-notifications';
import { GlobalService } from 'src/app/app.global.service';

@Component({
  selector: 'app-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.less']
})
export class AlertComponent implements OnInit, OnDestroy {

  public alerts: Alert[];
  public subscription: Subscription;

  /*
  Alert Component constructor it uses AlertService, ToastrManager and ViewContainerRef
  */
  constructor(
    private alertService: AlertService,
    public toastr: ToastrManager,
    public vcr: ViewContainerRef
  ) {
    this.alerts = new Array<Alert>();
  }

  /*
  Alert Component ngOnInit
  */
  ngOnInit() {
    this.subscription = this.alertService.getAlert().subscribe(alert => {
      this.addAlertToQueue(alert);
    });
  }

  /*
  It shows differnt types of alerts
  */
  addAlertToQueue(alert: Alert): any {
    switch (alert.type) {
      case AlertType.Success:
        this.showSuccess(alert);
        break;

      case AlertType.Information:
        this.showInfo(alert);
        break;

      case AlertType.Error:
        this.showError(alert);
        break;

      case AlertType.Warning:
        this.showWarning(alert);
        break;
    }
  }

/*
unsubscribe to ensure no memory leaks
 */
  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  /*
  Show success message
  */
  showSuccess(alert: Alert) {
    this.toastr.successToastr(
      alert.message,
      'Success',
      {
        toastLife: GlobalService.alertTimeOut,
        showCloseButton: true,
        maxShown: 10,
        newestOnTop: true
      });
  }

  /*
  Show error message
  */
  showError(alert: Alert) {
    this.toastr.errorToastr(
      alert.message,
      'Error',
      {
        toastLife: GlobalService.alertTimeOut,
        showCloseButton: true,
        maxShown: 10,
        newestOnTop: true
      });
  }

  /*
  Show warning message
  */
  showWarning(alert: Alert) {
    this.toastr.warningToastr(
      alert.message,
      'Warning',
      {
        toastLife: GlobalService.alertTimeOut,
        showCloseButton: true,
        maxShown: 10,
        newestOnTop: true
      });
  }

  /*
  Show info message
  */
  showInfo(alert: Alert) {
    this.toastr.infoToastr(
      alert.message,
      'Information',
      {
        toastLife: GlobalService.alertTimeOut,
        showCloseButton: true,
        maxShown: 10,
        newestOnTop: true
      });
  }

}
