import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminLoginComponent } from './components/admin-login/admin-login.component';
import { StudentsListComponent } from './components/students-list/students-list.component';
import { MainPageComponent } from './components/main-page/main-page.component';
import { OnBoardingViewComponent } from './components/on-boarding/on-boarding-view/on-boarding-view.component';
import { OnBoardingFormComponent } from './components/on-boarding/on-boarding-form/on-boarding-form.component';

const routes: Routes = [
  { path: 'login', component: AdminLoginComponent },
  {
    path: 'mainPage', component: MainPageComponent,
    children: [
      { path: '', redirectTo: 'onBoardingForm', pathMatch: 'full' },
      { path: 'onBoardingForm', component: OnBoardingFormComponent },
      {
        path: 'studentsList', component: StudentsListComponent,
        children: [
          { path: '', redirectTo: 'studentsList', pathMatch: 'full' },
        ]
      },
      { path: 'onBoardingForm/:id', component: OnBoardingFormComponent },
      { path: 'onBoardingViewForm/:id', component: OnBoardingViewComponent },
    ]
  },
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  // { path: '**', component: RegisterComponent }
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
