import { Component, OnInit, Input } from '@angular/core';
import { ConfirmDialogService } from 'src/app/services/confirm-dailog/confirm-dialog.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.less']
})

/*
  This is the general header component
*/
export class HeaderComponent implements OnInit {

  @Input() title: string;
  admin: string;
  constructor(private dialogService: ConfirmDialogService, private router: Router) { }

  ngOnInit() {
    this.admin = localStorage.getItem('currentUser');
  }
  callingLogout() {
    this.dialogService.openConfirmDialog('Do you want to Logout?')
      .afterClosed().subscribe(res => {
        if (res) {
          localStorage.clear();
          this.router.navigate(['login']);
        }
      });
  }
}
